import React, { useEffect, useState } from 'react'
import { useSelector, } from 'react-redux'
import { AVAILABLE_ASSETS, BlockPopup, RPC_URL_BY_CHAIN, EXPLORER_TXN_URL_LIST, ToastPopup, addressRegex, API_URL, SITE_LINK, IMAGE_URL } from '../../actions/constant'
import truncateEthAddress from 'truncate-eth-address'
import TextField from '../common/TextField'
import axios from 'axios'
import { useParams } from 'react-router-dom'
import ConnectWallet from "../WalletConnect/web3modal"

import { getCoinBalance, getMarketPrice, getTokenBalance, sendCoin, sendTokenFromContract } from '../../actions/common'



const ViewQrInvoice = () => {

    const { chainId, connected, signer, provider, address } = useSelector(state => state.web3)
    const { user } = useSelector(state => state.auth)

    const { id } = useParams()

    const [data, setdata] = useState({})
    const [setting, setsetting] = useState([])

    const [selectedasset, setselectedasset] = useState(null)
    const [selectedtype, setselectedtype] = useState(null)
    const [filterAsset, setfilterAsset] = useState([])
    const [assetBalance, setAssetBalance] = useState(0)
    const [currentPrice, setcurrentPrice] = useState(0)
    const [balloading, setballoading] = useState(false)
    //
    const [txnStatus, settxnStatus] = useState({})

    const [sendToken, setSendToken] = useState("")





    useEffect(() => {
        getData()
    }, [])

    const getData = async () => {
        BlockPopup.standard('.viewqrinvoice', `Please wait...`);
        try {
            let result = await axios.post(API_URL + `/api/qrpayment/view/`, { id })
            BlockPopup.remove('.viewqrinvoice');
            if (result.data) {
                setdata(result.data)
                setsetting(result.data.usersetting)

            }

        } catch (err) {
            BlockPopup.remove('.viewqrinvoice');
            ToastPopup.failure("Error Ocuured in API")
        }
    }















    var logoImg = "assets/images/logo.png"
    if (setting.qrLogo) {
        logoImg = IMAGE_URL + setting.qrLogo
    }






    return (
        <div>
            <div className="modal-dialog modal-md">
                <div className="modal-content viewqrinvoice">
                    <div className="modal-body">
                        <div>
                            <div>
                                <div className="row g-0">
                                    <div className="col-md-12">
                                        <div className='text-right'>
                                            {data.status === "Paid" ? <span className={`badge badge-soft-success p-1`} style={{ width: 100 }}>{data.status}</span> : <span className={`badge badge-soft-danger p-1`} style={{ width: 100 }}>{data.status}</span>}
                                        </div>
                                        <div className="p-xl-1 p-3">
                                            <div className="text-center mb-2">

                                                <img
                                                    src={logoImg}
                                                    className="align-self-center"
                                                    alt="logo-img"
                                                    height={40}
                                                />

                                            </div>
                                            <h3 className="h3 mb-0 mt-3 text-center">Payment ID :#{data.invoiceNo}</h3>
                                            <p className="text-muted mt-1 mb-4 text-center">
                                                Effortlessly View and Swiftly Pay Your Payments!
                                            </p>

                                            <div className='text-center'>
                                                <h1 class="">{data.invoiceAmount} {data.currencySymbol}</h1>
                                                <p class="text-muted">Amount Paid</p>
                                            </div>


                                            <div className="mb-3">
                                                <div className="row">


                                                    {/* {{SHOW WHEN PAIF}} */}
                                                    {data.status === "Paid" && <div className="col">
                                                        <ul className="checkout-list">

                                                            <li>
                                                                <span>Asset Paid</span>
                                                                <span className="checkout-total">{parseFloat(data.paidAmount).toFixed(4)} {data.assetSymbol}</span>
                                                            </li>
                                                            <li>
                                                                <span>{selectedasset} Price </span>
                                                                <span className="checkout-balance text-primary">{data.assetPrice} {data.assetSymbol}</span>
                                                            </li>

                                                            <li>
                                                                <span>Payer Address</span>
                                                                <span className="checkout-balance text-primary">{data.payerWalletAddress && truncateEthAddress(data.payerWalletAddress)}</span>
                                                            </li>
                                                            {/* <li>
                                                                <span>Status</span>
                                                                <span className={`badge badge-soft-success p-1`} style={{ width: 100 }}>{data.status}</span>
                                                            </li> */}


                                                        </ul>
                                                    </div>}

                                                </div>



                                            </div>




                                            <div className='poweredby' style={{ textAlign: 'right' }}>
                                                <img
                                                    src="assets/images/poweredby.png"
                                                    className="align-self-end"
                                                    alt="logo-img"
                                                    height={40}
                                                />
                                            </div>
                                        </div>
                                    </div>

                                </div>
                            </div>{" "}
                            {/* end card-body */}
                        </div>
                    </div>
                </div>
            </div>
        </div>

    )
}

export default ViewQrInvoice
