import { ethers } from "ethers";
import { ToastPopup } from "./constant";
import axios from "axios";
import TokenABI from "../contracts/abi.json"

export const getCoinBalance = async (signer) => {
    const balvalue = await signer.getBalance();
    const result = ethers.utils.formatUnits(balvalue, 18); // 18 is the number of decimal places
    return result
}

export const getTokenBalance = async (contractAddress, address, signer) => {

    const tokencontract = new ethers.Contract(contractAddress, TokenABI, signer);
    const assetrate = await tokencontract.balanceOf(address);
    const convertedprice = ethers.utils.formatUnits(assetrate, 'ether');
    return convertedprice
}

export const getTokenSymbol = async (contractAddress, signer) => {

    const tokencontract = new ethers.Contract(contractAddress, TokenABI, signer);
    const assetsymbol = await tokencontract.symbol();
    return assetsymbol
}

export const copyTextToClipboard = (text) => {
    navigator.clipboard.writeText(text)
        .then(() => {
            ToastPopup.success("Copied")
        })
        .catch((error) => {
            ToastPopup.failure("Error copying text to clipboard:")
        });
}


export const getMarketPrice = async (ids, defaultcurrency = "USD") => {
    try {

        let result = await axios.get(`https://api.coingecko.com/api/v3/simple/price?ids=${ids}&vs_currencies=${defaultcurrency}&x_cg_demo_api_key=CG-uTaKZVjZTx7tzeHj1BkVXj4A`)
        let temp = result.data;
        var marketPrice = temp[ids][defaultcurrency.toLowerCase()]
        // console.log("TEMP DATA", temp)
        return parseFloat(marketPrice ? marketPrice : 1).toFixed(4)
    } catch (err) {
        console.log("errr", err)
        return 0
    }


}


export const sendCoin = async (provider, signer, sendAddress, sendValue) => {
    const transactionHash = await signer.sendTransaction({
        to: sendAddress,
        value: ethers.utils.parseEther(sendValue),
    });
    console.log("transactionHash", transactionHash.hash)
    const [txhash, status] = await getTxnStatus(transactionHash.hash, provider);
    return { txhash, status }
}


export const sendTokenFromContract = async (provider, signer, contractAddress, sendAddress, sendValue) => {
    const contract = new ethers.Contract(contractAddress, TokenABI, signer);
    const recipientAddress = sendAddress;
    console.log("sendAddress", sendAddress)
    console.log("sendValue", sendValue)

    const amount = ethers.utils.parseUnits(sendValue, '18');
    const transactionHash = await contract.transfer(recipientAddress, amount);
    console.log("transactionHash", transactionHash.hash)
    const [txhash, status] = await getTxnStatus(transactionHash.hash, provider);
    return { txhash, status }
}


const sleep = (milliseconds) => {
    return new Promise(resolve => setTimeout(resolve, milliseconds))
}


export const getTxnStatus = async (txHash, provider) => {
    let transactionReceipt = null
    while (transactionReceipt == null) { // Waiting expectedBlockTime until the transaction is mined
        transactionReceipt = await provider.getTransactionReceipt(txHash);
        await sleep(3000)
    }
    if (transactionReceipt.status) {
        return [txHash, true];
    } else {
        return [txHash, false];
    }
}